	module.exports = {"data":{"wordpressPost":{"title":"The First Lady Of The US Melania Trump Has Got Style","content":"<p>Melania Trump hasn&#8217;t always been a style icon. Indeed, she&#8217;s worn some pretty dodgy outfits in the past, but hey, who hasn&#8217;t. I look back at some of the classy items I got about in in my earlier years and wonder what on earth I was thinking. Perhaps this is where Melania&#8217;s head is at right now?</p>\n<p>We can neither confirm nor deny that. What we can say with conviction however is that Melania has lifted her sartorial game &#8211; and in a big way. In fact, she&#8217;s looking so chic these days that plenty of high-end designers are actually clamouring to dress her.</p>\n<p>Take Victoria Beckham for example. Victoria has declared she would love to dress Melania.<br />\nIndeed, it would seem that she already is dressing the FLOTUS &#8211; in a manner of speaking.<br />\nAccording to Fox News VB revealed that Melania Trump wore something from VB&#8217;s range recently after having purchased it from her clothing store. And Victoria has zero issue with that.</p>\n<img class=\"alignnone size-full wp-image-3461\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/11/04220555/JPG-Melania-shoe.jpg\" alt=\"\" width=\"929\" height=\"597\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/11/04220555/JPG-Melania-shoe.jpg 929w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/11/04220555/JPG-Melania-shoe-300x193.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/11/04220555/JPG-Melania-shoe-768x494.jpg 768w\" sizes=\"(max-width: 929px) 100vw, 929px\" />\n<p>“I just think if any woman chooses to wear me, or invest in me as a designer, that’s incredibly flattering,” Vcitoria was quoted as saying.<br />\nThe FLOTUS chose a Beckham-designed navy blue turtleneck that sells for US $1050. She wore it with a cropped navy pant and her usual sky high heels &#8211; also in navy. And she looked incredible. It’s not the first time she’s been photographed in a VB design. In October she wore a US $1850 sleeveless cream sheath dress when being interview by Anderson Cooper.</p>\n<p>&nbsp;</p>\n<img class=\"alignnone size-full wp-image-3460\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/11/04220042/JPG-Anderson-Cooper.jpg\" alt=\"\" width=\"596\" height=\"594\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/11/04220042/JPG-Anderson-Cooper.jpg 596w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/11/04220042/JPG-Anderson-Cooper-150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/11/04220042/JPG-Anderson-Cooper-300x300.jpg 300w\" sizes=\"(max-width: 596px) 100vw, 596px\" />\n<p>&nbsp;</p>\n<p><strong>Melania and Anderson &#8211; and that dress. Chic. No more, no less.Image: Instagram/@Melaniatrump.style</strong></p>\n<p>Turns out Ivanka Trump is a VB fan too. She was snapped recently wearing a US $35 dress from Victoria’s Target collection.<br />\nAs a talented designer and also, savvy business woman, Victoria has no qualms about that. Indeed, she’d be happy to dress the FLOTUS and any members of the family if asked directly.</p>\n<p>“I like to make everybody happy, and whoever it is, if they want to wear my clothes and I can make that happen, and the sample is free, then I would jump at the opportunity to do that,” said Victoria.<br />\nGiven that information is now out in the open I think we can look forward to seeing the Trump women wearing plant more Victoria Beckham in the near future.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/11/04220028/JPG-Melania-split.jpg"},"categories":[{"slug":"lifestyle","name":"Lifestyle"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"6e9571f9-0077-5527-9abb-fb9eed33d4eb"}}

